import React, { PureComponent } from 'react';
import { uniqueId } from '@splunk/dashboard-utils/uniqueId';
import T from 'prop-types';
import { Options as Opts } from '@splunk/visualization-encoding/Options';
import console from '@splunk/dashboard-utils/console';
import withSplunkTheme from '@splunk/themes/withSplunkTheme';
import optionsSchema from './ConnectedLineOptionsSchema';
import editorConfig from './ConnectedLineEditorConfig';
import {
    computeArrowTriangle,
    computeFromArrowAngle,
    computeToArrowAngle,
    getNewFrom,
    getNewTo,
    computePathTriangle,
} from './utils/lineUtils';

const rootSvgStyle = {
    pointerEvents: 'none',
    overflow: 'visible',
};

const lineStyle = {
    pointerEvents: 'auto',
};

const dataContract = {
    requiredDataSources: ['primary'],
    initialRequestParams: {},
};

const vizConfig = {
    optionsSchema,
    editorConfig,
    dataContract,
};

class ConnectedLine extends PureComponent {
    static propTypes = {
        /**
         * Starting Coordinates of the Line
         */
        from: T.shape({
            x: T.number,
            y: T.number,
        }),
        /**
         * Ending Coordinates of the Line
         */
        to: T.shape({
            x: T.number,
            y: T.number,
        }),
        /**
         * Visualization formatting options
         */
        options: T.shape({
            strokeColor: T.string,
            strokeDataSeries: T.string,
            strokeDataPoint: T.string,
            strokeOpacity: T.number,
            strokeWidth: T.number,
            strokeDasharray: T.number,
            fromArrow: T.bool,
            toArrow: T.bool,
        }),
        /**
         * on line select callback
         */
        onLineSelect: T.func,
        dataSources: T.object,
        context: T.object,
        splunkTheme: T.object,
    };

    static defaultProps = {
        options: {
            strokeOpacity: 1,
            strokeWidth: 1,
            strokeDasharray: 0,
            strokeDataSeries: '> primary | seriesByType("number")',
            strokeDataPoint: '> strokeDataSeries | lastPoint()',
            fromArrow: false,
            toArrow: false,
        },
        from: {
            x: 0,
            y: 0,
        },
        to: {
            x: 150,
            y: 0,
        },
        onLineSelect: () => {},
        dataSources: [],
        context: {},
    };

    static config = vizConfig;

    // deprecated
    static schema = optionsSchema;

    // deprecated
    static dataContract = dataContract;

    // deprecated
    static editor = editorConfig;

    render() {
        const {
            from,
            to,
            onLineSelect,
            options,
            dataSources,
            context = {},
            splunkTheme,
        } = this.props;

        let renderOptions = {};
        try {
            renderOptions = Opts.evaluate(
                {
                    context,
                    options: {
                        ...ConnectedLine.defaultProps.options,
                        ...options,
                    },
                },
                dataSources
            );
        } catch (e) {
            console.error(
                `Unexpected error evaluating line options: ${e.message}`
            );
        }

        const themeStrokeColor =
            splunkTheme.colorScheme === 'light' ? '#000000' : '#ffffff';

        const { w, h, l } = computePathTriangle(from, to);

        const arrowL = 5 * renderOptions.strokeWidth;
        const { arrowW, arrowH } = computeArrowTriangle(w, h, l, arrowL);
        const squashedArrows =
            arrowL > l && renderOptions.fromArrow && renderOptions.toArrow;

        const fromArrowAngle = computeFromArrowAngle(from, to);
        const toArrowAngle = computeToArrowAngle(from, to);

        const fromArrowId = uniqueId();
        const toArrowId = uniqueId();

        const newW = w + arrowL;
        const newH = h + 2 * arrowL + 10;

        const { newFrom } = getNewFrom(
            from,
            to,
            arrowW,
            arrowH,
            renderOptions.fromArrow
        );
        const { newTo } = getNewTo(
            from,
            to,
            arrowW,
            arrowH,
            renderOptions.toArrow
        );

        return (
            <svg width={newW} height={newH} style={rootSvgStyle}>
                <path
                    data-test="arrows"
                    d={
                        squashedArrows
                            ? `M${newTo.x} ${newTo.y} L${newFrom.x} ${newFrom.y}`
                            : `M${newFrom.x} ${newFrom.y} L${newTo.x} ${newTo.y}`
                    }
                    stroke={renderOptions.strokeColor || themeStrokeColor}
                    strokeOpacity={`${renderOptions.strokeOpacity}`}
                    strokeWidth={`${renderOptions.strokeWidth}`}
                    strokeDasharray={`${renderOptions.strokeDasharray}`}
                    fill="none"
                    markerStart={
                        renderOptions.fromArrow ? `url(#${fromArrowId})` : ''
                    }
                    markerEnd={
                        renderOptions.toArrow ? `url(#${toArrowId})` : ''
                    }
                />
                <path
                    data-test="line"
                    onMouseDown={onLineSelect}
                    d={`M${from.x} ${from.y} L${to.x} ${to.y}`}
                    strokeWidth={`${renderOptions.strokeWidth + 20}`}
                    fill="none"
                    stroke="white"
                    strokeOpacity={0}
                    style={lineStyle}
                />
                <defs>
                    <marker
                        id={`${fromArrowId}`}
                        markerWidth="10"
                        markerHeight="10"
                        refX={squashedArrows ? '5' : '0'}
                        refY="2"
                        orient={`${fromArrowAngle}deg`}
                        markerUnits="strokeWidth"
                    >
                        <path
                            d="M0,0 L0,4 L5,2 z"
                            fill={renderOptions.strokeColor || themeStrokeColor}
                            fillOpacity={`${renderOptions.strokeOpacity}`}
                        />
                    </marker>
                    <marker
                        id={`${toArrowId}`}
                        markerWidth="10"
                        markerHeight="10"
                        refX={squashedArrows ? '5' : '0'}
                        refY="2"
                        orient={`${toArrowAngle}deg`}
                        markerUnits="strokeWidth"
                    >
                        <path
                            d="M0,0 L0,4 L5,2 z"
                            fill={renderOptions.strokeColor || themeStrokeColor}
                            fillOpacity={`${renderOptions.strokeOpacity}`}
                        />
                    </marker>
                </defs>
            </svg>
        );
    }
}

const ConnectedLineWithTheme = withSplunkTheme(ConnectedLine);
ConnectedLineWithTheme.propTypes = ConnectedLine.propTypes;
ConnectedLineWithTheme.defaultProps = ConnectedLine.defaultProps;
ConnectedLineWithTheme.config = ConnectedLine.config;
ConnectedLineWithTheme.schema = optionsSchema;
ConnectedLineWithTheme.editor = editorConfig;
export default ConnectedLineWithTheme;
